.media
{
    display: flex;
    flex-direction: column;
    width: 200px;
    padding: 5px;
    margin: 5px 0;
    background-color: #282c34;
    border-radius: 10px;
    position: relative;
    font-family: "Montserrat",sans-serif;
    cursor: pointer;
}
.media:hover
{
    background-color:white;
    color: black;
    transition: 2s;
    transform: 0.1s;
    }
@media(max-width:550px)
{
    .media
    {
        width: 46%;
    }
}
.poster
{
    border-radius: 10px;
}
.title
{
    width: 100%;
    text-align: center;
    font-size: 17px;
    padding: 8px 0;

}
.subTitle
{
    display: flex;
    justify-content: space-between;
    padding-bottom: 3px;
    padding: 0 2px;
    }